interface DocumentSidebarProps {
  currentMenu: 'upload' | 'list' | 'dashboard';
  onMenuSelect: (menu: 'upload' | 'list' | 'dashboard') => void;
}

export default function DocumentSidebar({ currentMenu, onMenuSelect }: DocumentSidebarProps) {
  return (
    <div className="h-full flex flex-col">
      <div className="p-4 border-b border-gray-200 dark:border-gray-700">
        <h2 className="text-xl font-bold text-gray-800 dark:text-gray-200">문서 관리</h2>
      </div>
      
      <div className="p-4 space-y-2">
        <button
          onClick={() => onMenuSelect('upload')}
          className={`w-full py-3 px-4 rounded-lg transition-colors flex items-center gap-2
            ${currentMenu === 'upload' 
              ? 'bg-blue-500 text-white' 
              : 'text-gray-700 dark:text-gray-200 hover:bg-gray-100 dark:hover:bg-gray-800'
            }`}
        >
          <UploadIcon />
          문서 등록/삭제
        </button>

        <button
          onClick={() => onMenuSelect('list')}
          className={`w-full py-3 px-4 rounded-lg transition-colors flex items-center gap-2
            ${currentMenu === 'list'
              ? 'bg-blue-500 text-white'
              : 'text-gray-700 dark:text-gray-200 hover:bg-gray-100 dark:hover:bg-gray-800'
            }`}
        >
          <ListIcon />
          문서 조회
        </button>

        <button
          onClick={() => onMenuSelect('dashboard')}
          className={`w-full py-3 px-4 rounded-lg transition-colors flex items-center gap-2
            ${currentMenu === 'dashboard'
              ? 'bg-blue-500 text-white'
              : 'text-gray-700 dark:text-gray-200 hover:bg-gray-100 dark:hover:bg-gray-800'
            }`}
        >
          <DashboardIcon />
          대시보드
        </button>
      </div>
    </div>
  );
}

const UploadIcon = () => (
  <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
    <path d="M21 15v4a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2v-4" />
    <polyline points="17 8 12 3 7 8" />
    <line x1="12" y1="3" x2="12" y2="15" />
  </svg>
);

const ListIcon = () => (
  <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
    <line x1="8" y1="6" x2="21" y2="6" />
    <line x1="8" y1="12" x2="21" y2="12" />
    <line x1="8" y1="18" x2="21" y2="18" />
    <line x1="3" y1="6" x2="3.01" y2="6" />
    <line x1="3" y1="12" x2="3.01" y2="12" />
    <line x1="3" y1="18" x2="3.01" y2="18" />
  </svg>
);

const DashboardIcon = () => (
  <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
    <rect x="3" y="3" width="7" height="7" />
    <rect x="14" y="3" width="7" height="7" />
    <rect x="14" y="14" width="7" height="7" />
    <rect x="3" y="14" width="7" height="7" />
  </svg>
); 